import React from 'react';
import '../../../components/Auth.scss';
import TextField from '@material-ui/core/TextField';
import iconGoogle from '../../../assets/img/Google.svg';
import { Link } from 'react-router-dom';
import { useForm } from "react-hook-form";
// import { Form, Formik } from "formik";

export default function SignIn() {

   // const { register, handleSubmit, errors } = useForm(); // initialise the hook
   // const onSubmit = event => {
   //    // event.preventDefault();
   //   console.log(event);
   // };
   
   const { handleSubmit, register, errors } = useForm();
   const onSubmit = values => {
     console.log(values);
   };

   return (
      <div className="container">
         <h4>Welcome to Invision</h4>
            <form onSubmit={handleSubmit(onSubmit)}>
               <div className="input">
                  <TextField
                     id="standard-error-helper-text"
                     name="user"
                     fullWidth 
                     // error={errors.user}
                     label="Users name or email"
                     // helperText={errors.user ? "Users name or Email is required" : ""}
                     // ref={register({ required: true })}
                     inputRef={register({ 
                        required: 'User is Required',
                        pattern: {
                           message: "invalid email address"
                        }
                     })}
                  />
                  { errors.user && errors.user.message }
               </div>
               <div className="input">
                  <TextField
                     id="standard-error-helper-text"
                     name="password"
                     fullWidth
                     // error={errors.password}
                     type="password"
                     label="Password"
                     // helperText={errors.password ? "Password is required" : ""}
                     // ref={register({ required: 'Required' })}
                  />
                  {/* {errors.password && 'Password is required'} */}
               </div>
               
               <Link to="/forget" className="forgot">Forgot Password?</Link>
               
               <div className="submit">
                  <button type="submit">Sign in</button>
               </div>
               <div className="division">
                  <span></span>
                  <div>Or</div>
                  <span></span>
               </div>
               <div className="singIn-google">
                  <button>
                     <img src={iconGoogle} alt="Google Logo" />
                     Sign in with Google
                  </button>
               </div>
               <p className="new-account">New Invision? <Link className="sing-up" to="/SignUp">Create Account</Link></p> 
            </form>
      </div>
   );
}